.section-banner-home {
    position: relative;

    .thumb {
        padding-bottom: 30%;
    }

    .swiper-pagination {
        text-align: right;
        padding-right: 40px;
    }

    .swiper-pagination-bullet {
        background: none;
        border: 1px solid #fff;
        margin: 0 3px !important;
    }

    .swiper-pagination-bullet-active {
        background: #fff;
    }
}

.home-search {
    position: absolute;
    top: 0;
    left: calc(50% - 475.5px);
    width: 960px;
    z-index: 9;

    .home-search-tab {
        margin-bottom: 0;

        li {
            display: inline-block;
            width: 159.33px;
            border-radius: 5px 5px 0 0;
            font-size: 14px;
            line-height: 20px;
            text-align: center;
            text-transform: uppercase;
            background: #fff;
            opacity: .85;
            color: #777;
            padding: 12px 0 6px 0;

            &.actived,
            &:hover {
                background: rgba(0, 0, 0, .65);
                color: #fff;
                cursor: pointer;
            }
        }
    }

    .home-search-tool {
        margin: 45px auto 0 auto;
        width: 100%;
    }

    .home-search-content {
        background: rgba(0, 0, 0, .65);
        box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
        border-radius: 0 5px 5px 5px;
        padding: 9px;
        width: 100%;
        margin: 0 auto;

        .home-search-control {
            background: #fff;
            border-radius: 3px 5px 5px 3px;
            display: flex;
            position: relative;
            margin-bottom: 10px;

            .search-cate {
                width: 190px;
                height: 42px;
                box-sizing: border-box;
                position: relative;
                cursor: pointer;
                border-right: 1px solid #ececec;

                .select-custom {
                    font-weight: bold;
                    color: #555;
                    border-radius: 3px 0 0 3px;
                    background-image: url(images/icons/icon-home-search.png);
                    background-repeat: no-repeat;
                    background-position: 10px center;
                    width: 100%;
                    height: 100%;
                    position: relative;
                    font-size: 14px;
                    display: block;
                    line-height: 40px;
                    background-size: 20px 21px;

                    &::after {
                        content: '';
                        background: url(images/icons/icon-down-arrow.png) no-repeat center center;
                        position: absolute;
                        right: 15px;
                        top: 12px;
                        width: 15px;
                        height: 18px;
                        background-size: 15px 18px;
                    }

                    #lblCurrCate {
                        white-space: nowrap;
                        width: 119px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        display: inline-block;
                        margin-left: 40px;
                        font-size: 14px;
                        line-height: 42px;
                    }
                }

                .advance-select-options {
                    position: absolute;
                    top: 42px;
                    left: 0;
                    width: 226px;
                    background: #fff;
                    box-sizing: border-box;
                    display: none;
                    z-index: 10000;
                    padding: 0;
                    box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
                    border-radius: 0;
                    min-width: 226px !important;
                    border-left: 1px solid #12a9d9;
                    border-right: 1px solid #12a9d9;
                    border-bottom: 1px solid #12a9d9;

                    ul {
                        max-height: inherit;
                        max-width: inherit;
                        min-width: 226px !important;

                        li {
                            cursor: pointer;
                            padding: 0;
                            margin: 0 14px;

                            span {
                                display: block;
                                border-bottom: 1px dashed #c4c4c4;
                                font-size: 13px;
                                line-height: 29px;
                                color: #686868;
                            }

                            img {
                                margin-right: 6px;
                                vertical-align: middle;
                                width: 16px;
                            }

                            ul {
                                li {
                                    position: relative;
                                    margin: 0;

                                    span {
                                        padding-left: 25px;
                                        margin-right: 14px;
                                    }
                                }
                            }
                        }
                    }
                }
            }

            .search-input {
                width: 610px;
                width: calc(100% - 190px);
                padding-right: 150px;
                position: relative;
            }

            input[type=text] {
                width: 100%;
                height: 42px;
                padding: 12px 15px;
                font-size: 14px;
                box-sizing: border-box;
                border: 0;
            }

            .search-button {
                width: 140px;
                position: absolute;
                right: 0;

                .btn-home-search {
                    width: 140px;
                    background: #3c9f10 url(images/icons/icon-search.png) no-repeat 20px 12px;
                    border-radius: 0 5px 5px 0;
                    height: 42px;
                    text-align: center;
                    color: #fff;
                    border: 0;
                    display: block;
                    font-weight: bold;
                    font-size: 16px;
                    padding-left: 25px;
                    margin: 0;
                    background-size: 16px 16px;
                    box-sizing: border-box;
                }
            }
        }
    }

    .home-filter {
        display: flex;

        .form-group {
            width: 21%;
            padding-right: 10px;
            margin-bottom: 0;

            .select2-container {
                width: 100% !important;
            }

            .select2-selection {
                height: 40px;
                border: 1px solid #777;
                border-radius: 3px;
                background: none;

                &__rendered {
                    line-height: 40px;
                    color: #fff;
                }

                &__arrow {
                    background: url(https://file4.batdongsan.com.vn/images/newhome/icon3x/icon-down-arrow.png) no-repeat center center;
                    background-size: 15px 18px;
                    width: 15px;
                    height: 18px;
                    top: 11px;
                    right: 11px;

                    b {
                        display: none
                    }
                }
            }
        }

        .search-action {
            width: 16%;

            a {
                color: #fff;
                line-height: 40px;
                vertical-align: middle;
                display: inline-block;
                font-size: 14px;

                img {
                    margin-right: 5px;
                    vertical-align: middle;
                }
            }

            .action-reset-search-form {
                margin-left: 10px;
            }
        }
    }

    @media (max-width: 1025px) {
        position: static;
        margin: 0 auto;
        max-width: 100%;
        padding: 0 15px;

        .home-search-tool {
            margin: 25px auto 0 auto;
        }

        .home-search-tab {
            li {
                border: 1px solid #777;
                border-bottom: 0;

                &.actived,
                &:hover {
                    border: 1px solid rgba(0, 0, 0, 0.65);
                }
            }
        }
    }

    @media (max-width: 991px) {
        padding: 0;

        .home-search-content {
            background: #fff;
            box-shadow: none;
            padding: 0;
        }

        .home-search-tab {
            display: flex;
            margin-bottom: 15px;

            li {
                border: 1px solid #e5e5e5;
                border-bottom: 1px solid #e5e5e5;
                border-radius: 0;
                margin-right: -1px;
                font-weight: bold;
                padding: 10px 0;

                &.actived {
                    border: 1px solid #e5e5e5;
                }
            }
        }

        .home-search-content {
            .home-search-control {
                margin-bottom: 15px;

                input[type=text] {
                    border: 1px solid #e5e5e5;
                }

                .search-input {
                    padding-right: 140px;
                }

                .search-cate {
                    .select-custom {
                        border: 1px solid #e5e5e5;
                    }
                }
            }
        }

        .home-filter {
            flex-wrap: wrap;

            .form-group {
                width: 50%;
                margin-bottom: 15px;

                .select2-selection {
                    border: 1px solid #e5e5e5;

                    &__rendered {
                        color: #222
                    }

                }
            }

            .search-action {
                width: 100%;

                a {
                    color: #222;
                    border: 1px solid #e5e5e5;
                    border-radius: 4px;
                    padding: 0 15px;
                }
            }
        }
    }


    @media (max-width: 414px) {
        .home-search-tab {
            li {
                width: 110px;
                font-size: 11px
            }
        }

        .home-search-content {
            .home-search-control {
                flex-wrap: wrap;

                .search-cate {
                    width: 100%;
                    margin-bottom: 15px;
                    border-right: 0;
                }

                .search-input {
                    width: 100%;
                    padding-right: 50px;
                }

                .search-button {
                    bottom: 0;
                    width: 50px;

                    .btn-home-search {
                        font-size: 0;
                        width: 50px;
                    }
                }
            }
        }

        .home-filter {
            .form-group {
                width: 100%;
                padding-right: 0;
            }
        }
    }
}

