.search-bar {
    box-shadow: 0 4px 8px rgba(3, 44, 71, .12);
    display: flex;
    align-items: center;
    padding: 0px 40px;
    margin: 0 -40px;
    border-top: 1px solid #e5e5e5;
    -webkit-transition-duration: 300ms;
    transition-duration: 300ms;
    -webkit-transition-property: all;
    transition-property: all;
    -webkit-transition-timing-function: cubic-bezier(.7, 1, .7, 1);
    transition-timing-function: cubic-bezier(.7, 1, .7, 1);

    &-tab {
        display: flex;
        align-items: center;

        a {
            color: #999;
            border: 1px solid #dee2e6;
            cursor: pointer;
            background: #fff;
            font-weight: 500;
            font-size: 14px;
            line-height: 20px;
            border-radius: 4px;
            padding: 5px;
            width: 85px;
            text-align: center;

            &:last-child {
                border-top-left-radius: 0;
                border-bottom-left-radius: 0;
            }

            &:first-child {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
            }

            &:not(:last-child) {
                margin-right: -1px;
            }

            &.actived {
                color: #2c2c2c;
                border: 1px solid #dee2e6;
                background: #f2f2f2;
            }
        }
    }

    &-suggestion {
        width: 277px;
        margin-left: 15px;
        position: relative;
        padding-right: 15px;
        margin-right: 15px;

        &::before {
            content: "";
            background: #e5e5e5;
            width: 1px;
            height: 30px;
            position: absolute;
            right: 0;
        }
    }

    .home-filter {
        display: flex;
        align-items: center;

        .form-group {
            padding-right: 15px;
            margin-right: 15px;
            margin-bottom: 0;
            position: relative;
            &::before {
                content: "";
                background: #e5e5e5;
                width: 1px;
                height: 30px;
                position: absolute;
                right: 0;
                top: 6px;
            }

            .lbl {
                display: block;
                font-size: 11px;
                color: #2c2f36;
                top: 5px;
                position: absolute;

                span {
                    position: relative;
                    padding-right: 20px;
                    background: url(images/icons/ic_caret_down.svg) no-repeat right -2px;
                }
            }
            .select2-container{
                height: 50px;
            }
            .select2-selection {
                height: 32px;
                border: 0;
                border-radius: 3px;
                background: none;
                padding-top: 20px;
                &__rendered {
                    line-height: 32px;
                    color: #000;
                    padding: 0;
                }

                &__arrow {
                    display: none
                }
            }
        }

        .filter-control {
            position: relative;
            margin-right: 15px;

            .select-control-label {
                line-height: 50px;
                padding: 0 10px;
                cursor: pointer;

                &:hover {
                    background: #f5f6f7;
                }
            }

            .select-filter-dropbox {
                box-shadow: 0px 20px 34px rgba(13, 66, 38, 0.2);
                background: #FFFFFF;
                position: absolute;
                z-index: 2;
                top: 53px;
                padding: 15px;
                width: 350px;
                right: 0;
                display: none;

                .form-group {
                    margin-right: 0;
                    padding-right: 0;
                    margin-bottom: 15px;

                    &::before {
                        display: none
                    }
                }

                .phong-ngu {
                    display: flex;

                    li {
                        width: 20%;
                        border: 1px solid #ccd3d9;
                        padding: 0;
                        text-align: center;
                        margin-left: -1px;
                        position: relative;
                        z-index: 1;
                        line-height: 40px;
                        cursor: pointer;

                        &:first-child {
                            border-left: 1px solid #ccd3d9;
                            border-radius: 4px 0 0 4px;
                            margin-left: 0 !important;
                        }

                        &:hover {
                            background: #edf7ff;
                        }

                        &:active {
                            background: #ebedf0;
                        }
                    }
                }

                .border-top {
                    margin: 10px -15px 0 -15px;
                    padding: 15px 15px 0 15px
                }
            }
        }

        .refesh {
            margin-right: 15px;
        }

        #btnSearch {
            background: #004e7f;
            border: 1px solid #004e7f;
            color: #fff;
            border-radius: 4px;
            line-height: 40px;
            padding: 0 10px;
        }
    }

    @media (max-width: 1500px) {
        &-suggestion {
            width: 200px;
        }
    }

    @media (max-width: 1366px) {
        .home-filter {
            .form-group {
                margin-right: 10px;
                padding-right: 10px;
            }
        }
    }

    @media (max-width: 1280px) {
        padding: 0 15px;
        margin: 0 -15px;
        &-tab{
            a{
                padding: 5px 2px;
                width: 75px;
            }
        }

    }

    @media (max-width: 1199px) {
        &-tab {
            display: none
        }
        &-suggestion{
            margin-left: 0;
        }
        .home-filter {
            .filter-control{
                margin-right: 5px;
                .re__text{
                    font-size: 0;
                }
            }
        }
    }
    @media (max-width: 1025px) {
        .home-filter {
            .form-group{
                padding-right: 8px;
                margin-right: 8px
            }
        }
    }
    @media (max-width: 991px) {
        display: block;
        margin: 0;
        &-tab{
            display: block;
            width: 100%;
            margin-top: 30px;
            margin-bottom: 15px;
            a{
                width: 110px;
                padding: 10px 0;
                display: inline-block;
                text-transform: uppercase;
            }
        }
        &-suggestion{
            width: 100%;
            padding-right: 0;
            margin-left: 0;
            margin-bottom: 15px;
            &::before{display: none;}
            .form-search{
                .icon_seach_web{
                    top: 4px;
                }
                .input_search{
                    height: 40px;
                }
            }
        }
        .home-filter{
            flex-wrap: wrap;
            margin-right: -15px;
            .form-group{
                width: 33.33%;
                width: calc(33.33% - 15px);
                margin-right: 15px;
                border: 1px solid #e5e5e5;
                padding: 5px 10px 0 10px;
                margin-bottom: 15px;
                &::before{
                    display: none;
                }
            }
            .filter-control{
                svg{
                    color: #222;
                }
                .re__text{
                    font-size: 14px;
                }
            }
        }
    }
    @media (max-width:576px) {
        .home-filter {
            .form-group{
                width:50%;
                width: calc(50% - 15px);
            }
        }
    }
    @media (max-width:414px) {
        .home-filter {
            .form-group{
                width:100%;
            }
        }
    }
}



